import AboutUsBg1 from "../../../../static/images/AboutUsBg1.jpg";
import AboutUsImage1 from "../../../../static/images/AboutUsImage1.jpg";
import AboutUsImage2 from "../../../../static/images/AboutUsImage2.jpg";
import Shop1 from "../../../../static/images/Shop1.jpg";
import Shop2 from "../../../../static/images/Shop2.jpg";
import Shop3 from "../../../../static/images/Shop3.jpg";

import styles from "./index.module.scss";

const AboutUsLayout = () => (
  <div className={styles.wrapper}>
    <div className={styles.banner}>
      <img src={AboutUsBg1} alt="background-pokemons" />
    </div>

    <div className={styles.pokeshop}>
      <h1 className={styles.title}>What is PokShop?</h1>

      <div className={styles.contentContainer}>
        <div className={styles.content}>
          <p>
            PokemonCenter.com is our official online store for Pokmon Center
            merchandise, focusing on unique designs and high quality products
            for all ages. Pokmon launched in Japan in 1996 and today is one of
            the most popular childrens entertainment properties in the world.
          </p>
          <p>
            Whether you are a player, collector, parent, professor, or fan, we
            strive to offer items featuring a wide range of Pokmon to allow you
            to express yourself.
          </p>
          <p>
            Were always open. Come say hello and take a look at the wide world
            of Pokmon!
          </p>
        </div>

        <div className={styles.image}>
          <img src={AboutUsImage1} alt="background-pokemons" />
        </div>
      </div>
    </div>

    <div className={styles.products}>
      <h2 className={styles.title__center}>PokShop Products</h2>

      <div className={styles.contentContainer}>
        <div className={styles.image}>
          <img width={250} src={AboutUsImage2} alt="background-pokemons" />
        </div>

        <div className={styles.content}>
          <p>
            Managed by The Pokmon Company International, PokShop offers an
            authentic collection cards
          </p>
          <p>
            The Pokmon Company International, a subsidiary of The Pokmon
            Company in Japan, manages the property outside of Asia and is
            responsible for brand management, licensing, marketing, the Pokmon
            Trading Card Game, the animated TV series, home entertainment, and
            the official Pokmon website.
          </p>
          <p>
            True to the Pokmon spirit, the Pokmon Center products are made to
            our high standards.
          </p>
        </div>
      </div>
    </div>

    <div className={styles.story}>
      <h1 className={styles.title__center}>The PokShop Story</h1>

      <div className={styles.content__story}>
        <p>
          PokShop is inspired by the Pokmon video game, where Pokmon Centers
          are important destinations that help Trainers on their journey. In
          that tradition, physical Pokmon Centers were first introduced in
          Japan by The Pokmon Company and continues to thrive in a new online
          form at PokemonCenter.com.
        </p>
        <p>
          More than a dozen physical Pokmon Centers serve Pokmon fans in
          Japan. These Pokmon Centers are a nation-wide retail chain that
          offers a wide range of Pokmon products in major Japanese cities and
          central transit hubs.
        </p>
      </div>

      <div className={styles.images}>
        <img src={Shop1} alt="shop-1" />
        <img src={Shop2} alt="shop2" />
        <img src={Shop3} alt="shop-3" />
      </div>
    </div>
  </div>
);

export default AboutUsLayout;
